<template>
    <div class="deliverryOfNewPreDetail">
        <!-- 头部 -->
        <navigation-bar @onLeftClick="onLeftClick" :pageName="title" >
           
       </navigation-bar>

        <section class="contentBox">
            <div class="content">
                <div v-html="content.PROCESS_CONTENT" class="con"></div>
            </div>
        </section>
       
        <!-- 底部按钮 -->
        <section class="bottomBtn">
           <submit-btn :submitName="btnName" @save='submit' ></submit-btn>
       </section>

    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
import SubmitBtn from '@/common/currency/SubmitBtn';
import {listHouseDeliveDetail,addPvUvRecord} from '@/services/servers/DeliveryOfNewPremises'

export default {
    name:'DeliverryOfNewPreDetail',
    components:{
        NavigationBar,
        SubmitBtn
    },
    mounted(){
        this.initData();
        if(!this.$route.query.info){
            this.enter();
        }
    },
    data(){
        return{
            title:'新房交付',
             userInfo:{
                "OWNER_CODE":"2019103016101113758",
                "AREA_CODE":"201901090001",
                "ROOM_CODE":"20190610",
                "OWNER_NAME": "5679_yz",
                "OWNER_PHONE": "13793295679",
            },
          
            btnName:'问卷调查',
            content:{},
        }
    },
    methods:{
        onLeftClick(){
            this.$router.go(-1);
        },
        initData(){
            let data = {
                "OWNER_CODE":this.$userInfo.OWNER_CODE,
                "ROOM_CODE":this.$userInfo.ROOM_CODE,
                "AREA_CODE":this.$userInfo.AREA_CODE,
            };
            data = {data:JSON.stringify(data)};
            listHouseDeliveDetail(data).then(
                res => {
                   this.content =  res
                },
                err => {
                    console.log(err)
                }
            )
        },
        submit(value){
            console.log(value)
             /*点击问卷调查按钮进入问卷调查页面*/
            this.$router.push({
                name: 'questionnaireList',
                query: {
                 ENROL_CODE : this.content.ENROL_CODE || '',
                 PUBLISH_CODE : this.content.PUBLISH_CODE || '',
                }
            })
        },
        enter(){
            let data = {
                "OWNER_CODE":this.$userInfo.OWNER_CODE,
                "ROOM_CODE":this.$userInfo.ROOM_CODE,
                "AREA_CODE":this.$userInfo.AREA_CODE,
            };
            data = {data:JSON.stringify(data)}
            addPvUvRecord(data).then(
                res =>{

                },
                err =>{

                }
            )
        }
    }
}
</script>
<style scoped>
.deliverryOfNewPreDetail{
    width: 100%;
    height: 100%;
    font-size: 16px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
}
.deliverryOfNewPreDetail .contentBox{
    width: 100%;
    flex: 1;
    padding-top: 4px;
    overflow: hidden;
}
.deliverryOfNewPreDetail .content{
    padding: 0 3px 72px 7px;
    background-color: #fff;
    overflow: hidden;
    width: calc(100% - 10px);
}
.deliverryOfNewPreDetail .con p{
    word-break: break-all;
    word-wrap: break-word;
}
.deliverryOfNewPreDetail .bottomBtn{
    width: 100%;
    height: 70px;
    line-height: 70px;
    overflow: hidden;
    margin-top: 20px;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 200;
    bottom: 2%;
}
</style>